.advantages {
  width: 100%;
  padding-top: 120px;

  @media (max-width: $desktop-width) {
    padding-top: 80px;
  }
}

.advantages__wrapper {
  max-width: 1252px;
  width: 100%;
  box-sizing: border-box;
  padding: 0 15px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: $desktop-width) {
    padding: 0 50px;
  }
}

.advantages__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: 1222px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;

  @media (max-width: $desktop-width) {

  }
}

.advantages__item {
  width: 100%;
  max-width: 596px;
  position: relative;
  margin: 0;
  box-sizing: border-box;
  min-height: 300px;
  border: 1px solid $main-grey;
  padding: 40px 45px 35px;

  &:hover {
    background-color: $main-light-blue;
    border-color: $main-light-blue;

    h3 {
      color: $main-white;
    }

    p {
      color: $main-white;
    }

    svg {
      fill: $main-white;
    }
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 5px;
    height: 100.5%;
    top: -1px;
    left: -1px;
    background-color: $main-light-blue;
  }

  svg {
    fill: $main-light-blue;
  }

  h3 {
    font-family: $roboto-condensed;
    color: $main-light-blue;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    margin: 20px 0 15px;
  }

  p {
    margin: 0;
  }

  @media (max-width: $desktop-width) {
    min-height: 360px;
  }
}
